<script>
export default {
  name: 'Gauzhin',
  data () {
    return {
      yun_list:[],
      diao_list:{},
      open: '',
    }
  },
  methods:{
    route(path){
      this.$router.push({path:'/gau/gaulist/'+path})
    },
  },
  // after render
  mounted: function(){
    var env = this
    this.$store.gaulist_page = 1
    this.$http.post('/open/yun').then(function(resp){
      if(resp.data.status=='success'){
        env.yun_list = resp.data.yun_list
        env.diao_list = resp.data.diao_list
      }
    })
  },
}
</script>

