<script>
export default {
  name: 'Psy',
  data () {
    return {
      diao_list:{},
      open: '',
    }
  },
  methods:{
    route(path){
      this.$router.push({path:'/psy/'+path})
    },
  },
  // after render
  mounted: function(){
    var env = this

    this.diao_list = this.$store.psy_diao_list || []
    if(this.diao_list.length==0){
      this.$http.post('/open/yun/psy').then(function(resp){
        if(resp.data.status=='success'){
          env.diao_list = resp.data.diao_list
          env.$store.psy_diao_list = env.diao_list
        }
      })
    }

    // this.$http.post('/open/yun/psy').then(function(resp){
    //   if(resp.data.status=='success'){
    //     env.diao_list = resp.data.diao_list
    //   }
    // })
  },
}
</script>

