<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵部" :disabled="false"><router-link :to="{ name: 'Gauzhin' }">博白音校正</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵" :disabled="true">{{yun}}韵</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    
    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="zi in zi_list" :key="zi.idx" class="bspan">
        <router-link :to="{ name: 'Gauzi', params:{zi: zi.idx}}">
        <!--<mu-button fab small color="gray" style="background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;">
            {{zi.zi}} 
        </mu-button>--><span>{{zi.jian}}{{zi.zi}}</span> <span>{{zi.q_mu}}{{zi.q_yun}}/{{zi.g_mu}}{{zi.g_yun}}</span> <span>{{zi.frq}}</span><span>{{zi.fan2}}</span> <span>{{zi.szt}}</span> <span>{{zi.bb_yin}}</span> <span>{{zi.bb_yin2}}</span>
        </router-link>
        </span>
      </mu-col>
    </mu-row>

    <mu-row>
      <mu-pagination :total="pagin.total" :page-size="pagin.per_page" :current.sync="pagin.page" :page-count="pagin.per_count"  @change="pagin_change"></mu-pagination>
    </mu-row>

</mu-container>
</template>

<script>
export default {
  name: 'Gaulist',
  data () {
    return {
      zi_list:[],
      yun: '',
      pagin:{total:0, page:1, per_page:20, per_count:5}
    }
  },
  methods:{
    pagin_change(page){
      this.pagin.page = page
      this.$store.gaulist_page = page
      this.load_data()
    },
    load_data(){
      var env = this
      this.yun = this.$route.params.yun
      var page = this.$store.gaulist_page || this.pagin.page
      this.$http.post('/open/yun/list/'+this.yun+'?page='+page).then(function(resp){
        if(resp.data.status=='success'){
          env.zi_list = resp.data.zi_list
          env.pagin.total = resp.data.total
          env.pagin.page = resp.data.page
          env.pagin.per_page = resp.data.per_page
        }
      })
    },
  },
  // after render
  mounted: function(){
    this.load_data()
  },
}
</script>

<style scoped>
.bspan{float: left;
    width: 100%;
    display: block;
    height: 50px;}
.bspan span{margin-left:10px;}
</style>
