<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="拆字" :disabled="false"><router-link :to="{ name: 'Chaizi' }">拆字</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵" :disabled="true">{{word}}字</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row>
        <mu-col span="9">
            <input type="text" v-model="word" ref="zi" placeholder="请输入要拆的字或部首或笔画" class="my-input" @keyup.enter="search"  id="search" @focus="focusInput"/>
        </mu-col>
        <mu-col span="3">
            <mu-button color="primary" @click="search" class="btn-new"><mu-icon value="search"></mu-icon>搜索</mu-button>
        </mu-col>
    </mu-row>

    <mu-breadcrumbs divider="—" class="split">
      <mu-breadcrumbs-item   :key="1" :disabled="true">拆分字部件</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="(sub,idx) in sub_list" :key="idx">
          <mu-button class="zi" flat :data-clipboard-text="sub" :id="'sub'+idx" @click="copy_text('sub'+idx)">{{sub}}</mu-button>
        </span>
      </mu-col>
    </mu-row>

    <mu-breadcrumbs divider="—" class="split">
      <mu-breadcrumbs-item   :key="3" :disabled="true">字部件索引</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="(zi,idx) in subs_list" :key="idx">
            <mu-button fab small class="zi" @click="search_word(zi)">{{zi}}</mu-button>
        </span>
      </mu-col>
    </mu-row>

    <mu-breadcrumbs divider="—" class="split">
      <mu-breadcrumbs-item   :key="2" :disabled="true">字形相似字</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="(zi,idx) in zi_list" :key="idx">
            <router-link :to="{ name: 'Search', params:{zi: zi[0]}}">
              <mu-button fab small class="zi">{{zi}}</mu-button>
            </router-link>
        </span>
      </mu-col>
    </mu-row>

</mu-container>
</template>

<script>
export default {
  name: 'Chaizi',
  data () {
    return {
      zi_list:[],
      sub_list: [],
      subs_list: [],
      word: '',
      zi:'',
    }
  },
  methods:{
    focusInput(){
       
    },
    copy_text(id) {
       var env = this;
       var clipboard = new this.clipboard('#'+id);
       clipboard.on('success', function () {
         clipboard.destroy()
       });
    },
    search_word(zi){
      this.word = zi
      this.search()
    },
    search(e){
        var env = this
        this.zi_list = env.$store.chaizi_zi_dict[env.word] || []
        this.sub_list = env.$store.chaizi_sub_dict[env.word] || []
        this.subs_list = env.$store.chaizi_subs_dict[env.word] || []

        if(!(this.zi_list === undefined) && this.zi_list.length!==0){
          return
        }
        this.$http.get('/open/chaizi/'+this.word).then(function(resp){
            if(resp.data.status=='success'){
                env.zi_list = resp.data.zi_list
                env.sub_list = resp.data.sub_list
                env.subs_list = resp.data.subs_list
                env.$store.chaizi_zi_dict[env.word] = env.zi_list
                env.$store.chaizi_sub_dict[env.word] = env.sub_list
                env.$store.chaizi_subs_dict[env.word] = env.subs_list
            }else{
                env.$toast.error('找不到这个字');
            }
        })
    },
  },
  // after render
  mounted: function(){
    var env = this
    this.word = this.$route.params.word

    if(this.$store.chaizi_zi_dict===undefined){
      this.$store.chaizi_zi_dict = {}
      this.$store.chaizi_sub_dict = {}
      this.$store.chaizi_subs_dict = {}
    }

    if(!this.word){
      return
    }

    this.zi_list = this.$store.chaizi_zi_dict[this.word] || []
    this.sub_list = env.$store.chaizi_sub_dict[env.word] || []
    this.subs_list = env.$store.chaizi_subs_dict[env.word] || []
    if(this.zi_list.length>0){
      return
    }

    this.$http.post('/open/chaizi/'+this.word).then(function(resp){
      if(resp.data.status=='success'){
        env.zi_list = resp.data.zi_list
        env.sub_list = resp.data.sub_list
        env.subs_list = resp.data.subs_list
        env.$store.chaizi_zi_dict[env.word] = env.zi_list
        env.$store.chaizi_sub_dict[env.word] = env.sub_list
        env.$store.chaizi_subs_dict[env.word] = env.subs_list
      }else{
          env.$toast.error('找不到这个字');
      }
    })
  },
}
</script>

<style scoped>
.zi{background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;}
.btnspan{text-transform: none;}
</style>
