<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="集詞" :disabled="false"><router-link :to="{ name: 'CollectCu' }">集詞</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="詞" :disabled="true">{{word}}</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row>
        <mu-col span="9">
            <input type="text" v-model="word" ref="zi" placeholder="请输入要查询的詞" class="my-input" @keyup.enter="search"  id="search" @focus="focusInput"/>
        </mu-col>
        <mu-col span="3">
            <mu-button color="primary" @click="search" class="btn-new"><mu-icon value="search"></mu-icon>搜索</mu-button>
        </mu-col>
    </mu-row>

    <mu-tabs :value.sync="tab" inverse color="secondary" text-color="rgba(0, 0, 0, .54)" center @change="load_tab">
      <mu-tab>查詢結果</mu-tab>
      <mu-tab>新增詞語</mu-tab>
    </mu-tabs>


    <mu-breadcrumbs divider="—" class="split" v-if="tab === 0">
      <mu-breadcrumbs-item   :key="2" :disabled="true">相關的詞</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;" v-if="tab === 0">
      <mu-col span="12">
        <div v-for="(w,idx) in cu_list" :key="idx" class="block">
            <router-link :to="{ name: 'CollectWord', params:{word: w.word}}">
              <mu-button flat class="zi" :data-clipboard-text="w.word" :id="'w'+idx" @click="copy_text('w'+idx);search_word(w.word)"><b>{{w.word}}</b></mu-button> 
              <mu-button flat class="zi" :data-clipboard-text="w.pinyin" :id="'py'+idx" @click="copy_text('py'+idx);search_word(w.pinyin)"><b>{{w.pinyin}}</b></mu-button> 
              <mu-button flat :data-clipboard-text="w.yi" :id="'yi'+idx" @click="copy_text('yi'+idx)">義: <b>{{w.yi}}</b></mu-button> 
            </router-link>
        </div>
      </mu-col>
    </mu-row>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;" v-if="tab === 1">
      <mu-col span="12">
          <input type="text" v-model="cu.word" ref="word" placeholder="字，詞, 短語" class="my-input" />
      </mu-col>
      <mu-col span="12">
          <input type="text" v-model="cu.pinyin" ref="pinyin" placeholder="拼音，每個字之間的拼音用空格格開" class="my-input" />
      </mu-col>
      <mu-col span="12">
          <mu-text-field v-model="cu.yi" multi-line :rows="6" :rows-max="24" style="width:100%;background:white;text-indent:12px;" placeholder="釋義"></mu-text-field>
      </mu-col>
      <mu-col span="12">
        <div style="text-align:center;padding:20px;width:100%;">
          <mu-button color="primary" style="margin:0px auto;" @click="do_post"  class="btn-new">確定</mu-button><br/><br/>
        </div>
      </mu-col>
    </mu-row>

</mu-container>
</template>

<script>
export default {
  name: 'Pinyin',
  data () {
    return {
      cu_list:[],
      word: '',
      zi:'',
      tab: 0,
      cu: {word:'', pinyin:'', lang:'hakka', province:'廣西', city:'玉林', area:'博白', town:'鳳山', pian:'桂南片博白腔', yi:''},
    }
  },
  methods:{
    focusInput(){
       
    },
    copy_text(id) {
       var env = this;
       var clipboard = new this.clipboard('#'+id);
       clipboard.on('success', function () {
         clipboard.destroy()
       });
    },
    search_word(zi){
      this.word = zi
      this.search()
    },
    search(e){
        var env = this
        env.tab = 0
        this.$http.get('/open/cu/'+this.word).then(function(resp){
            if(resp.data.status=='success'){
                env.cu_list = resp.data.cu_list
            }else{
                env.$toast.error('找不到这个詞');
                env.cu_list = []
            }
        })
    },
    load_tab(val){
      var env = this
      env.tab = val
    },
    do_post(){
      var env = this
      this.$http.post('/open/cu', this.cu).then(function(resp){
            if(resp.data.status=='success'){
                env.$toast.success('新增成功');
                env.tab = 1
                env.word = env.cu.word
                env.search()
            }else{
                env.$toast.error('找不到这个詞');
            }
      })
    }
  },
  // after render
  mounted: function(){
    var env = this
  },
}
</script>

<style scoped>
.zi{background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;text-transform: none;}
.block{background-color:#fefefe;margin-bottom:5px;text-transform: none;color:#333;width: 100%;display: block; padding: 5px;}
.mu-text-field-textarea{text-indent:12px;}
</style>
