import Vue from 'vue'

import Router from 'vue-router'
Vue.use(Router)

import Index from '@/components/Index'
import Donate from '@/components/Donate'

import Search from '@/components/Search'
import Check from '@/components/poem/Check.vue'

import Yun from '@/components/poem/Yun.vue'
import Yunzi from '@/components/poem/Yunzi.vue'
import Psy from '@/components/poem/Psy.vue'
import Psyzi from '@/components/poem/Psyzi.vue'
import Czy from '@/components/poem/Czy.vue'
import Czyzi from '@/components/poem/Czyzi.vue'
import Chaizi from '@/components/poem/Chaizi.vue'
import Pinyin from '@/components/poem/Pinyin.vue'

// 校正
import Gauzhin from '@/components/poem/Gauzhin.vue'
import Gaulist from '@/components/poem/Gaulist.vue'
import Gauzi from '@/components/poem/Gauzi.vue'

// 集詞
import CollectCu from '@/components/CollectCu.vue'


export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/donate',
      name: 'Donate',
      component: Donate
    },
    {
      path: '/check',
      name: 'Check',
      component: Check
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/searchWord/:word',
      name: 'SearchWord',
      component: Search
    },

    {
      path: '/yun',
      name: 'Yun',
      component: Yun
    },
    {
      path: '/yun/:yun',
      name: 'Yunzi',
      component: Yunzi
    },
    {
      path: '/psy',
      name: 'Psy',
      component: Psy
    },
    {
      path: '/psy/:yun',
      name: 'Psyzi',
      component: Psyzi
    },
    {
      path: '/czy',
      name: 'Czy',
      component: Czy
    },
    {
      path: '/czy/:bu/:yun',
      name: 'Czyzi',
      component: Czyzi
    },

    // chaizi
    {
      path: '/chaizi',
      name: 'Chaizi',
      component: Chaizi
    },
    {
      path: '/chaiziWord/:word',
      name: 'ChaiziWord',
      component: Chaizi
    },

    // pinyin
    {
      path: '/pinyin',
      name: 'Pinyin',
      component: Pinyin
    },
    {
      path: '/pinyinWord/:word',
      name: 'PinyinWord',
      component: Pinyin
    },


    {
      path: '/gau/gauzhin',
      name: 'Gauzhin',
      component: Gauzhin
    },
    {
      path: '/gau/gaulist/:yun',
      name: 'Gaulist',
      component: Gaulist
    },
    {
      path: '/gau/gauzi',
      name: 'Gauzi',
      component: Gauzi
    },
    // 集詞
    {
      path: '/gau/CollectCu',
      name: 'CollectCu',
      component: CollectCu
    },
    {
      path: '/gau/CollectCu/:word',
      name: 'CollectWord',
      component: CollectCu
    }
  ]
})
