<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵部" :disabled="false"><router-link :to="{ name: 'Psy' }">平水韵部</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵" :disabled="true">{{yun}}韵</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    
    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="zi in zi_list" :key="zi.idx">
        <router-link :to="{ name: 'Search', params:{zi: zi.idx}}">
        <mu-button fab small color="gray" style="background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;">{{zi.zi}}</mu-button>
        </router-link>
        </span>
      </mu-col>
    </mu-row>
    
    <!--
    <mu-row v-for="fan in fan_keys" :key="fan" style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="2" style="padding-top: 10px;"><span>{{fan}}</span></mu-col>
      <mu-col span="10">
        <span v-for="zi in fan_list[fan]" :key="zi.idx">
        <router-link :to="{ name: 'Search', params:{zi: zi.idx}}">
        <mu-button fab small color="gray" style="background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;">{{zi.jian}}</mu-button>
        </router-link>
        </span>
      </mu-col>
    </mu-row>
    -->

</mu-container>
</template>

<script>
export default {
  name: 'Ysyzi',
  data () {
    return {
      zi_list:[],
      fan_list: {},
      fan_keys: [],
      yun: '',
    }
  },
  methods:{
  },
  // after render
  mounted: function(){
    var env = this
    this.yun = this.$route.params.yun

    if(this.$store.psy_dict===undefined){
      this.$store.psy_dict = {}
    }
    this.zi_list = this.$store.psy_dict[this.yun] || []
    if(this.zi_list.length>0){
      return
    }

    this.$http.post('/open/yun/psy/'+this.yun).then(function(resp){
      if(resp.data.status=='success'){
        env.zi_list = resp.data.zi_list
        env.$store.psy_dict[env.yun] = env.zi_list
        env.fan_list = resp.data.fan_list
        env.fan_keys = resp.data.fan_keys
      }
    })
  },
}
</script>

