import Vue from 'vue';
import MuseUI from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
import Toast from 'muse-ui-toast';
// MuseUI.theme.add('myTheme', {
//   primary: MuseUI.Colors.blue,
//   secondary: MuseUI.Colors.pinkA200,
//   success: MuseUI.Colors.green,
//   warning: MuseUI.Colors.yellow600,
//   info: MuseUI.Colors.blue,
//   error: MuseUI.Colors.red,
// }).use('myTheme');
Vue.use(MuseUI);
Vue.use(Toast);

import 'typeface-roboto'

import './assets/fonts.css'

import App from './App';
Vue.config.productionTip = false;

import router from './router'
import axios from 'axios'

var env = 'prod'
var baseURL = ''
if (env == 'dev'){
  baseURL = 'http://127.0.0.1:6002'
}else if(env == 'prod'){
  baseURL = 'http://47.96.118.117:5020'
}else if(env == 'prods'){
  baseURL = 'https://poem.m-front.cn'
}

var headers = {}
var http = axios.create({
    baseURL: baseURL,
    timeout: 50000,
    // headers:{'Authorization':'true'}
    headers: headers,
    withCredentials: true,
})
Vue.prototype.$http = http
Vue.prototype.$load_tenant = function(tenant) {
    var header = {}
    if(!tenant){
        tenant = localStorage.getItem('current_tenant')
        if(tenant){
            tenant = JSON.parse(tenant)
        }
    }
    if(tenant){
        header.Authorization = tenant.Authorization
    }
    return {headers:header}
}

import store from './store/store'
store.baseURL = baseURL

import clipboard from 'clipboard';
Vue.prototype.clipboard = clipboard;

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
