<script>
export default {
  name: 'Czy',
  data () {
    return {
      diao_list:{},
      open: '',
    }
  },
  methods:{
    route(bu, path){
      this.$router.push({path:'/czy/'+bu+'/'+path})
    },
  },
  // after render
  mounted: function(){
    var env = this

    this.diao_list = this.$store.czy_diao_list || []
    if(this.diao_list.length==0){
      this.$http.post('/open/yun/czy').then(function(resp){
        if(resp.data.status=='success'){
          env.diao_list = resp.data.diao_list
          env.$store.czy_diao_list = env.diao_list
        }
      })
    }

    // this.$http.post('/open/yun/czy').then(function(resp){
    //   if(resp.data.status=='success'){
    //     env.diao_list = resp.data.diao_list
    //   }
    // })
  },
}
</script>

