<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="拆音" :disabled="false"><router-link :to="{ name: 'Chaizi' }">拆音</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵" :disabled="true">{{word}}</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row>
        <mu-col span="9">
            <input type="text" v-model="word" ref="zi" placeholder="请输入要查询的拼音" class="my-input" @keyup.enter="search"  id="search" @focus="focusInput"/>
        </mu-col>
        <mu-col span="3">
            <mu-button color="primary" @click="search" class="btn-new"><mu-icon value="search"></mu-icon>搜索</mu-button>
        </mu-col>
    </mu-row>

    <mu-breadcrumbs divider="—" class="split">
      <mu-breadcrumbs-item   :key="2" :disabled="true">读音相似字</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-row style="border-bottom:1px solid #ddd;padding: 5px 10px;">
      <mu-col span="12">
        <span v-for="(zi,idx) in zi_list" :key="idx">
            <router-link :to="{ name: 'Search', params:{zi: zi[0]}}">
              <mu-button class="zi" flat :data-clipboard-text="zi" :id="'sub'+idx" @click="copy_text('sub'+idx)">{{zi}}</mu-button>
            </router-link>
        </span>
      </mu-col>
    </mu-row>

</mu-container>
</template>

<script>
export default {
  name: 'Pinyin',
  data () {
    return {
      zi_list:[],
      word: '',
      zi:'',
    }
  },
  methods:{
    focusInput(){
       
    },
    copy_text(id) {
       var env = this;
       var clipboard = new this.clipboard('#'+id);
       clipboard.on('success', function () {
         clipboard.destroy()
       });
    },
    search_word(zi){
      this.word = zi
      this.search()
    },
    search(e){
        var env = this
        this.$http.get('/open/pinyin/'+this.word).then(function(resp){
            if(resp.data.status=='success'){
                env.zi_list = resp.data.zi_list
            }else{
                env.$toast.error('找不到这个音');
            }
        })
    },
  },
  // after render
  mounted: function(){
    var env = this
  },
}
</script>

<style scoped>
.zi{background:#efefef;color:#333;margin:0px 10px 5px 0px;border-radius:10%;text-transform: none;}
.btnspan{text-transform: none;}
</style>
