<script>
export default {
  name: 'Search',
  data () {
    return {
      active_yi: -1,
      title: '查字',
      zi: '',
      object: {},
      yi_list:['', '', '', '', '', '', ''],
      ziyi: false,
    }
  },
  methods:{
    focusInput(){
       
    },
    copy_text(id) {
       var env = this;
       var clipboard = new this.clipboard('#'+id);
       clipboard.on('success', function () {
         clipboard.destroy()
       });
    },
    search(e){
        var env = this
        this.active_yi = -1
        var object = env.$store.zi_dict[env.zi]
        if(!(object === undefined)){
          env.object = object
          return
        }
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.$store.zi_dict[env.zi] = env.object
            }else{
                env.object = {}
                env.$toast.error('找不到这个字');
            }
        })
    },
    route(path){
      this.$router.push({path:path})
    },
    load_yi(val){
      var env = this
      if(val!=-1 && env.ziyi!=env.zi){
        this.$http.post('/open/zi/'+this.zi).then(function(resp){
          if(resp.data.status=='success'){
                env.yi_list = resp.data.data
                env.ziyi = env.zi
            }else{
                env.$toast.warning('查不到字义');
            }
        })
      }
    },
  },
  // // before render
  // created:function() {
  //   var env = this
  //   //console.log(this.$refs)
  // },
  // after render
  mounted: function(){
    var env = this
    this.zi = this.$route.params.zi
    if(this.$store.zi_dict===undefined){
      this.$store.zi_dict = {}
    }
    if(this.zi){
        var object = env.$store.zi_dict[env.zi]
        if(!(object === undefined)){
          env.object = object
          return
        }
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.$store.zi_dict[env.zi] = env.object
                env.zi = env.object.jian
            }else{
                env.object = {}
                env.$toast.error('找不到这个字');
            }
        })
    }
  },
}
</script>

