<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false"><router-link :to="{ name: 'Search' }">广韵</router-link></mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵" :disabled="true">作文</mu-breadcrumbs-item>
    </mu-breadcrumbs>

    <mu-tabs :value.sync="active_yi" inverse color="secondary" text-color="rgba(0, 0, 0, .54)" center @change="load_tab">
      <mu-tab>格律诗</mu-tab>
      <mu-tab>格律词</mu-tab>
      <mu-tab>对联</mu-tab>
    </mu-tabs>
    <div style="text-align:center;padding:20px;">
      <div class="demo-text" v-if="active_yi === 0">
       <mu-text-field v-model="data_shi" multi-line :rows="8" :rows-max="24" style="width:100%;background:white;"></mu-text-field>
       <mu-button color="primary" style="margin:0px auto;" @click="check"  class="btn-new">检查</mu-button><br/><br/>
       <mu-card style="background: #99CCFF;" ><div v-html="resp_shi"></div></mu-card>
      </div>
      <div class="demo-text" v-if="active_yi === 1">
         <mu-text-field v-model="data_ci" multi-line :rows="8" :rows-max="24" style="width:100%;background:white;"></mu-text-field>
         <mu-button color="primary" style="margin:0px auto;" @click="check"  class="btn-new">检查</mu-button><br/><br/>
         <mu-card style="background: #99CCFF;" ><div v-html="resp_ci"></div></mu-card>
      </div>
      <div class="demo-text" v-if="active_yi === 2">
         <mu-text-field v-model="data_dui" multi-line :rows="8" :rows-max="24" style="width:100%;background:white;"></mu-text-field>
         <mu-button color="primary" style="margin:0px auto;" @click="check"  class="btn-new">检查</mu-button><br/><br/>
         <mu-card style="background: #99CCFF;" ><div v-html="resp_dui"></div></mu-card>
      </div>
    </div>

</mu-container>
</template>

<script>
export default {
  name: 'Check',
  data () {
    return {
      ctype: 'shi',
      active_yi: 0,
      data_shi: '请输入',
      data_ci: '请输入',
      data_dui: '请输入',
      resp_shi: '',
      resp_ci: '',
      resp_dui: '',
    }
  },
  methods:{
    load_tab(val){
      if(val==0){
        this.ctype = 'shi'
      }else if(val==1){
        this.ctype = 'ci'
      }else if(val==2){
        this.ctype = 'dui'
      }
    },
    check(){
      var env = this
      var val = ''
      if(this.ctype == 'shi'){
        val = this.data_shi
      }
      if(this.ctype == 'ci'){
        val = this.data_ci
      }
      if(this.ctype == 'dui'){
        val = this.data_dui
      }

      if(this.ctype == 'shi' && !this.data_shi){
        env.$toast.warning('请先输入格律诗');
        return false
      }
      if(this.ctype == 'ci' && !this.data_ci){
        env.$toast.warning('请先输入词');
        return false
      }
      if(this.ctype == 'dui' && !this.data_dui){
        env.$toast.warning('请先输入对联');
        return false
      }

      this.$http.post('/open/check/'+this.ctype, {data:val}).then(function(resp){
        if(resp.data.status=='success'){
          if(env.ctype=='shi'){
            env.resp_shi = resp.data.data
          }
          else if(env.ctype=='ci'){
            env.resp_ci = resp.data.data
          }
          else if(env.ctype=='dui'){
            env.resp_dui = resp.data.data
          }
        }else{
          env.$toast.error('检查失败, 可能输入的数据或网络有问题~')
        }
      })
    },
  },
}
</script>

