<template>
  <div id="app">
    <div id="header">
      <mu-container>
        <mu-drawer :open.sync="drawer.open" :docked="drawer.docked" :right="drawer.position === 'right'">
            <mu-list>
              <mu-list-item button @click="route('/')">
                <mu-list-item-title><a>查字</a></mu-list-item-title>
              </mu-list-item>
              <mu-list-item button @click="route('/chaizi')">
                <mu-list-item-title><a>拆字</a></mu-list-item-title>
              </mu-list-item>
              <mu-list-item button @click="route('/yun')">
                <mu-list-item-title><a>广韵部</a></mu-list-item-title>
              </mu-list-item>
              <mu-list-item button @click="route('/psy')">
                <mu-list-item-title><a>平水韵</a></mu-list-item-title>
              </mu-list-item>
              <mu-list-item button @click="route('/czy')">
                <mu-list-item-title><a>词林正韵</a></mu-list-item-title>
              </mu-list-item>
              <mu-list-item button @click="route('/check')">
                <mu-list-item-title><a>写作检查</a></mu-list-item-title>
              </mu-list-item>
            </mu-list>
        </mu-drawer>
        <mu-appbar style="width: 100%;max-width:100%; position:fixed; left:0; top:0;z-index: 1000;" color="primary">
          <mu-button icon slot="left" @click="drawer.open = !drawer.open">
            <mu-icon value="menu"></mu-icon>
          </mu-button>
          <span>{{title}}</span>
          <mu-button flat slot="right" @click="route('/donate')">赞助</mu-button>
        </mu-appbar>
      </mu-container>
    </div>

    <div id="container">
      <router-view/>
    </div>

    <div id="footer">
      <mu-container>
        <mu-bottom-nav :shift="true" @change="route_nav" :value.sync="bottom_nav" style="position:fixed; left:0; bottom:0;width: 100%;z-index: 1000;">
          <mu-bottom-nav-item title="查字" value="search" icon="search"></mu-bottom-nav-item>
          <mu-bottom-nav-item title="广韵" value="yun" icon="view_module"></mu-bottom-nav-item>
          <mu-bottom-nav-item title="平水韵" value="psy" icon="view_quilt"></mu-bottom-nav-item>
          <mu-bottom-nav-item title="写作" value="check" icon="brush"></mu-bottom-nav-item>
        </mu-bottom-nav>
      </mu-container>
    </div>
  </div>
</template>
<script>
export default {
  name: 'app',
  data: function() {
    return {
      title: '广韵',
      bottom_nav: 'search',
      drawer: {
        open: false,
        docked: false,
        position: 'left',
        bottom_nav: 'search',
        /* h5+ app */
        h5_adjust : false,
        h5_domReady : false,
        /* end h5+ app */
      }
    }
  },
  methods: {
    route(path){
      this.$router.push({path:path})
      this.drawer.open = false
    },
    route_nav(val){
      if(val=='search'){
        this.$router.push({path:'/'})
      }else if(val=='yun'){
        this.$router.push({path:'/yun'})
      }else if(val=='psy'){
        this.$router.push({path:'/psy'})
      }else if(val=='check'){
        this.$router.push({path:'/check'})
      }
    },

    /* h5+ app */
    h5_compatibleAdjust(){
        if(this.h5_adjust||!window.plus||!this.h5_domReady){
          return;
        }
        var plus = window.plus

        this.h5_adjust=true;
        // iOS平台特效
        if('iOS'==plus.os.name){
          document.getElementById('content').className='scontent';  // 使用div的滚动条
          if(navigator.userAgent.indexOf('StreamApp')>=0){  // 在流应用模式下显示返回按钮
            document.getElementById('back').style.visibility='visible';
          }
        }
    },
    h5_plusReady(){
        if(!window.plus){
          console.log('window.plus not exists!')
          return;
        }
        var plus = window.plus

        var _this=this;
        // 隐藏滚动条
        plus.webview.currentWebview().setStyle({scrollIndicator:'none'});
        // Android处理返回键
        plus.key.addEventListener('backbutton',() => {
          _this.h5_back();
        },false);
        this.h5_compatibleAdjust();
        console.log("h5_plusReady")
    },
    h5_init(){
      var _this=this;
      document.addEventListener('touchstart',() => {
          return false;
      },true);
      // 禁止选择
      document.oncontextmenu=function(){
        return false;
      };
      if(window.plus){
        this.h5_plusReady();
      }else{
        document.addEventListener('plusready',this.h5_plusReady(),false);
      }
      document.addEventListener('DOMContentLoaded',() => {
        _this.h5_domReady=true;
        _this.h5_compatibleAdjust();
      },false);
    },
    h5_back() {
        if(this.h5_adjust||!window.plus||!this.h5_domReady){
          return;
        }
        var plus = window.plus

        // 增加项目中的主页用来跳转到微服务页面
        if(this.$route.path == '/') {
          this.h5_close();
        } else {
          this.$router.go(-1);
        }
    },
    h5_close() {
        if(this.h5_adjust||!window.plus||!this.h5_domReady){
          return;
        }
        var plus = window.plus

        try {
          if(this.$route.path == '/') {
            ('iOS'==plus.os.name)?plus.nativeUI.confirm('确认退出？', (e) => {
              if(e.index>0){
                plus.runtime.quit();
              }
            }, this.title, ['取消','确定']):(confirm('确认退出？')&&plus.runtime.quit());
          } else {
            this.$router.go(-1);
          }
        } catch(e) {
          console.log('返回插件调用失败');
        }
    },
    /* h5+ app */
  },
  beforeCreate: function() {
  },
  created: function(){
    var env = this
    if(this.$route.path != '/'){
      env.$router.push({path:'/'})
    }
  },
  mounted: function(){
    /* h5+ app */
    // this.h5_init()
    setTimeout(this.h5_init, 1000)
    /* end h5+ app */
  },
  watch: {
    $route(to, from){
      if(to.path.indexOf('/yun')>-1){
        this.bottom_nav = 'yun'
      }else if(to.path.indexOf('/psy')>-1){
        this.bottom_nav = 'psy'
      }else if(to.path.indexOf('/check')>-1){
        this.bottom_nav = 'check'
      }else{
        this.bottom_nav = 'search'
      }
    },
  },
}
</script>

