.my-input{
  width:100%;line-height:36px;height:36px;border:0px;text-indent:10px;
  border-bottom:2px solid #2196f3;
}
.my-input:focus{
  width:100%;line-height:36px;height:36px;border:0px;outline:0;text-indent:10px;
  border-bottom:2px solid #2196f3;user-select:text;
}

/*传统配色*/
.mu-primary-color{background-color: #8fb2c9;}
.mu-bottom-nav-shift{background-color: #5698c3;}
.index-card{background-color:#d8e3e7;}.psy-card{text-align:center; color:#4e7ca1;}
