<template>
<mu-container>
    <mu-breadcrumbs>
        <mu-breadcrumbs-item key="广韵" :disabled="false" class="red-font">广韵</mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="韵部" :disabled="false" v-if="object.idx">
            <router-link :to="{ name: 'Yunzi', params:{yun:object.gyun} }">{{object.gyun}}韵</router-link>
        </mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="查字" :disabled="true" v-if="!object.idx">查字</mu-breadcrumbs-item>
        <mu-breadcrumbs-item key="查字" :disabled="true" v-if="object.idx">{{object.zi}}{{object.jian}}</mu-breadcrumbs-item>
    </mu-breadcrumbs>
    <mu-row>
        <mu-col span="9">
            <!--
            <mu-text-field v-model="zi" ref="zi" placeholder="请输入要查的字或广韵字序" style="width:100%;" @keyup.enter="search"></mu-text-field>
            -->
            <input type="text" v-model="zi" ref="zi" placeholder="请输入要查的字或广韵字序" class="my-input" @keyup.enter="search" id="search" @focus="focusInput"/>
        </mu-col>
        <mu-col span="3">
            <mu-button color="primary" @click="search"  class="btn-new">
            <mu-icon value="search"></mu-icon>搜索</mu-button>
        </mu-col>
    </mu-row>

    <mu-card style="width: 100%; margin: 0 auto;" v-if="object.idx">
        <!--
      <mu-card-header title="Myron Avatar" sub-title="sub title">
        <mu-avatar slot="avatar">
          <img src="../../assets/images/uicon.jpg">
        </mu-avatar>
      </mu-card-header>
        -->
      <mu-card-title :title="object.zi" :sub-title="'简体: '+object.jian + ' /  广韵字序: '+object.idx"></mu-card-title>
      <mu-card-actions>
        <mu-button color="primary" flat @click="route('/yun/'+object.gyun)" >韵部: <b>{{object.gyun}}</b></mu-button>
        <mu-button flat>反切: <b>{{object.fan2}}</b></mu-button>
        <mu-button flat>广韵序: <b>{{object.gyxu}}</b></mu-button>
        <mu-button flat>声调: <b>{{object.diao2 || object.diao}}</b></mu-button>
        <mu-button flat>声形旁: <b>{{object.sheng || 'X'}}</b> / <b>{{object.xing||'X'}}</b> / <b>{{object.pang ||
         'X'}}</b></mu-button>

        <mu-button flat>摄: <b>{{object.she}}</b></mu-button> 
        <mu-button flat>呼: <b>{{object.hu}}</b></mu-button> 
        <mu-button flat>切韵拼音: <b>{{object.poem}}</b></mu-button> 
        <mu-button flat>中古拼音(polyhedron): <b>{{object.poly}}</b></mu-button>
        <mu-button flat>拼音: <b>{{object.py}}</b></mu-button>

        <span v-if="object.ps_yun_list"><br/>
        <mu-button color="primary" flat v-for="yun in object.ps_yun_list" :key="yun" @click="route('/psy/'+yun)">
          平水韵部: <b>{{yun}}</b>
        </mu-button>
        </span>

        <span v-if="object.cz_buyun_list"><br/>
        <mu-button color="primary" flat v-for="yun in object.cz_buyun_list" :key="yun[1]" @click="route('/czy/'+yun[0]+'/'+yun[1])">
          词林韵部: <b>{{yun[0]}} > {{yun[1]}}</b>
        </mu-button>
        </span>
      </mu-card-actions>
      <mu-card-text>
        <mu-tabs :value.sync="active_yi" inverse color="secondary" text-color="rgba(0, 0, 0, .54)" center @change="load_yi">
          <mu-tab>广韵释义</mu-tab>
          <mu-tab>汉语字典</mu-tab>
          <mu-tab>康熙字典</mu-tab>
          <mu-tab>说文解字</mu-tab>
        </mu-tabs>
        <!--
        <div class="demo-text" v-if="active_yi === 0">
          <p>{{ '释义: ' + (object.yi || 'X')}}</p>
        </div>
        -->
        <div class="demo-text" v-if="active_yi === 0">
          <p>{{ '释义: ' + (yi_list[6]||'') }}</p>
        </div>
        <div class="demo-text" v-if="active_yi === 1">
          <div v-html="yi_list[0]"></div>
        </div>
        <div class="demo-text" v-if="active_yi === 2">
          <div v-html="yi_list[1]"></div>
        </div>
        <div class="demo-text" v-if="active_yi === 3">
          <div v-html="yi_list[2]"></div>
        </div>

      </mu-card-text>
    </mu-card>

    <mu-card v-if="!object.idx">
        <p class="index-card">昔開皇初，有劉儀同臻、顏外史之推、盧武陽思道、李常伺若、蕭國子該、辛咨議德源、薛吏部道衡、委著作彥淵等八人，同詣法言門宿。夜永酒闌，論及音韻。古今聲調既自有別，諸家取捨亦復不同。吳楚則時傷輕淺，燕趙則多涉重濁；秦隴則去聲為入，梁益則平聲似去；又“支” “脂” “魚” “虞”共為一韻，“先” “仙” “尤” “侯”俱論是切。欲廣文路，自可清濁皆通；若賞知音，即須輕重有別。 呂靜《韻集》、夏侯該《韻略》、陽休之《韻略》、李季節《音譜》、杜臺卿《韻略》等，各有乖互。江東取韻與河北復殊。因論南北是非，古今通塞，欲更捃選精切，除削疏緩。顏外史、蕭國子多所決定。魏著作謂法言曰：“響來論難，疑處悉盡，何為不隨口記之，我輩數人，定則定矣。”即燈下握筆，略記綱紀。 后博問英辯，殆得精華。於是更涉馀學，兼從薄宦，十數年間，不遑修集。今返初服，私訓諸弟，凡有文藻，即須聲韻。屏居山野，交游阻隔，疑惑之處，質問無從。亡者則生死路殊，空懷可作之嘆；存者則貴賤禮隔，已報絕交之旨。遂取諸家音韻，古今字書，以前所記者定為《切韻》五卷。剖析毫氂，分別黍累。何煩泣玉，未可懸金。藏之名山，昔怪馬遷之言大；持之蓋醬，今嘆揚雄之口吃。非是小子專轍，乃述群賢遺意；寧敢施行人世，直欲不出戶庭。 於時歲次辛酉大隋仁壽元年（601）也。
        </p>
    </mu-card>
</mu-container>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      active_yi: -1,
      title: '查字',
      zi: '',
      object: {},
      yi_list:['', '', ''],
      ziyi: false,
    }
  },
  methods:{
    focusInput(){
       
    },
    search(e){
        var env = this
        this.$router.push({ name: 'Search', params:{zi: this.zi}})
    },
    route(path){
      this.$router.push({path:path})
    },
    load_yi(val){
      var env = this
      if(val!=-1 && env.ziyi!=env.zi){
        this.$http.post('/open/zi/'+this.zi).then(function(resp){
          if(resp.data.status=='success'){
                env.yi_list = resp.data.data
                env.ziyi = env.zi
            }else{
                env.$toast.warning('查不到字义');
            }
        })
      }
    },
  },
  // // before render
  // created:function() {
  //   var env = this
  //   //console.log(this.$refs)
  // },
  // after render
  mounted: function(){
    var env = this
    this.zi = this.$route.params.zi
    if(this.$store.zi_dict===undefined){
      this.$store.zi_dict = {}
    }
    if(this.zi){
        var object = env.$store.zi_dict[env.zi]
        if(!(object === undefined)){
          env.object = object
          return
        }
        this.$http.get('/open/zi/'+this.zi).then(function(resp){
            if(resp.data.status=='success'){
                env.object = resp.data.data
                env.$store.zi_dict[env.zi] = env.object
                env.zi = env.object.jian
            }else{
                env.object = {}
                env.$toast.error('找不到这个字');
            }
        })
    }
  },
}
</script>
<style scoped>
.container{padding: 0px 10px;}
</style>